import React, { Component } from 'react';
import { connect } from 'react-redux';

import FacebookLogin from 'react-facebook-login';
import * as actions from '../../actions';

const mapStateToProps = (state) => {
    const {
        auth: { token },
    } = state;
    return {
        token,
    };
};

const actionCreators = {
    convertToken: actions.convertToken,
};

class FacebookOauth extends Component {
    state = {
        response: null,
        failure: null,
    };

    responseFacebook = (response) => {
        const { convertToken } = this.props;
        this.setState({ response });
        convertToken(
            response.accessToken,
            'facebook',
            'h19R0p6UGmYKzuc4fg1uaPlHwXPOc8GktswMZEdu'
        );
    };

    onFailure = (failure) => {
        this.setState({ failure });
    };

    render() {
        return (
            <>
                <FacebookLogin
                    appId="369580627566109"
                    autoLoad={false}
                    fields="name,email,picture"
                    callback={this.responseFacebook}
                    onFailure={this.onFailure}
                    textButton="Login"
                    // isMobile={false}
                    // redirectUri={'https://sashakrauzer.github.io/'}
                    disableMobileRedirect={true}
                />
                {/* <p>
                    Response facebook: {JSON.stringify(this.state.response)}
                    <br />
                    Failure facebook: {JSON.stringify(this.state.failure)}
                </p> */}
            </>
        );
    }
}

export default connect(mapStateToProps, actionCreators)(FacebookOauth);
